 */
/* COLORS */
/* COLORS */
.dropdown {
  position: relative;
}

.dropdown__content {
  position: absolute;
  top: 100%;
  z-index: 10;
  padding: 20px;
  display: none;
  opacity: 0;
  will-change: opacity;
  background: #ffffff;
  box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
  transition-property: opacity, box-shadow;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.dropdown_open .dropdown__content {
  display: inline-flex;
}

.dropdown_visible .dropdown__content {
  opacity: 1;
}

/* COLORS */
.form__row {
  display: flex;
  margin-bottom: 20px;
}

.form__item {
  margin-right: 20px;
}

.form__item:last-child {
  margin-right: 0;
}

/* COLORS */
.modal {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  align-items: center;
  justify-content: center;
  z-index: 100;
  display: none;
  opacity: 0;
  will-change: opacity;
  transition-property: opacity;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.modal_open {
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal_visible {
  opacity: 1;
}

.modal__content {
  max-width: 500px;
  max-height: 500px;
  position: relative;
  background: #ffffff;
  overflow-y: auto;
  box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
  transition-property: box-shadow;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.modal__close {
  position: absolute;
  right: 16px;
  top: 16px;
  font-size: 20px;
  width: 16px;
  height: 16px;
  padding: 0;
}

.modal__content-header {
  padding: 16px 18px;
  font-size: 16px;
  line-height: 22px;
  color: #000000;
  background-color: #FFFFFF;
}

.modal__content-body {
  padding: 20px;
}

.modal-overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0);
  z-index: 99;
  display: none;
  will-change: background;
  transition-property: background;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.modal-overlay_open {
  display: block;
}

.modal-overlay_visible {
  background: rgba(0, 0, 0, 0.6);
}
